@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,700&display=swap'); 
@import url('https://fonts.googleapis.com/css2?family=Otomanopee+Ones:wght@1,700&display=swap');

.text_white{
    color:black;
    font-family: 'Open Sans', sans-serif;;
}

.wc{
  color:rgb(9, 153, 4);
  text-align: center;
  font-size: 20px;
  padding: 20px;
}

.btn {
    padding: 1rem
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 700px;
    border-radius: 1em;
    border: 5px rgb(38, 0, 255);
/*     height: 100vh;   */  
    font-family: 'Open Sans', sans-serif;
    padding: 20px; 
    margin-left: auto;
    margin-right: auto;
}

.card_container{
    display: flex;
    width: 100%;
    height: auto;
    background: rgb(189, 189, 187, .8);
    border-radius: 5px;
    box-shadow: 3px 3px 8px 4px rgba(0,0,0, .2);
}

.header{
    border-right: 1px solid #dddddd;
}

.description{
    background: transparent;
    width: 100%;
}

.header, .description{
     padding:20px;
    text-align: start;
}

.imagen{
    width:  350px;
    height: 250px;
    border-radius: 1rem;
}

.imgloader {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
  }

  .centerbut {
    display: flex;
    align-items: center;
    padding: .5em;
    font-size: large;
    border-radius: 1em;
    margin-left: auto;
    margin-right: auto;
    color:beige;
    background: blue;
  }

  @media screen and (max-width: 375px) {
  
    .wc{
        color:rgb(9, 153, 4);
        text-align: center;
        font-size: 10px;
        padding: 2px;
      }

    .container{
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 1em;
        border: 5px rgb(38, 0, 255);
     /*     height: 100vh;      */
        font-family: 'Open Sans', sans-serif;
        padding: 5px; 
    }
    
    .card_container{
        display: flex;
        width: 100%;
        height: auto;
        background: rgb(189, 189, 187, .8);
        border-radius: 5px;
        box-shadow: 3px 3px 8px 4px rgba(0,0,0, .2);
    }
    
    .description{
        background: transparent;
        width: 100%;
    }
    .imagen{
        width:  300px;
        height: 250px;
    }
  }
